.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  min-width: 320px;
  background-color: white;
  border-bottom: 1px solid #cecece;
}

.insta-logo {
  font-family: "Blue Vinyl";
  font-weight: bold;
  padding-right: 15px;
  font-size: 18px;
  color: black;
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 975px;
  padding-left: 15px;
  padding-right: 15px;

}

.nav-icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-size: 16px;
  //min-width: 50px;
}
